body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}



.header li, .header ul {
  display: inline;
  list-style-type: none;
  margin: 0;
}

.header {
  /*background-color: #111;*/
  padding: 0;
  margin: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-content: space-around;
          align-content: space-around;
  height: 64px;
}

.header li a {
  color: #666666;
  font-weight: bold;
  text-decoration: none;
  padding: 15px;
  display: inline-block;
  border-top: 4px solid white;
  width: 80px;
  text-align: center;
}

.header li a:hover {
  color: #a1271f;
  cursor: pointer;
  border-top: 4px solid #a1271f;
}

.header img {
  padding: 7px;
  -webkit-flex: 1 1;
          flex: 1 1;
  padding-left: 80px;
}

.headerMenu {
  -webkit-flex: 3 1;
          flex: 3 1;
}

.headerUser {
  -webkit-flex: 1 1;
          flex: 1 1;
}

.content {
  padding: 0px 200px;
}


.footer {
	position: fixed;
	left: 0;
	bottom: 0;
	height: 120px;
	width: 100%;
	text-align: center;
	border-top: 1px solid #cccccc;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: row;
	        flex-direction: row;
	padding: 0px 200px;
}

.copyright {
	color: #8c8c8c;
	-webkit-flex: 3 1;
	        flex: 3 1;
	padding-top: 20px;
}

.copyright > .sm {
	font-size: 14px;
	line-height: 0.3;
}

.copyright > p {
	text-align: left;
}

.socialMedia {
	-webkit-flex: 2 1;
	        flex: 2 1;
}
